import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'react-flexbox-grid';
import './Content.css';
import { language } from '../../config/constants';

class Content extends PureComponent {
    render() {
        const { lang } = this.props;
        return (
            <Row center="xs">
                <Col xs={10}>
                    <Row>
                        <Col xs={6} className={'wrap-card'}>
                            <h3 className={'title'}> {language[lang].profile} </h3>
                            <p>
                                Lorem ipsum dolor, sit amet consectetur adipisicing elit. Architecto quasi fugiat accusamus, in natus qui? Amet eius ullam quod nostrum fugit! Assumenda ea, temporibus culpa nam laborum asperiores cum officiis.
                            </p>
                        </Col>
                        <Col xs={6} className={'wrap-card'}>
                            <h3 className={'title'}> {language[lang].contact} </h3>
                            <p>
                                Lorem ipsum dolor sit, amet consectetur adipisicing elit. Cum, explicabo rerum ad animi voluptates, totam ex obcaecati rem aliquam id nisi sit, odit vel! Cum vel dicta nostrum quaerat repudiandae?
                            </p>
                        </Col>
                    </Row>
                </Col>
            </Row>
        );
    }
}

Content.propTypes = {
    
};

export default Content;


